
@import "../utilities/scssvariables.scss";

nav{
width: 100%;
height: 10%;
background-color: $wrap-bg-color;
position: absolute;
position: relative;
top: 0%;
z-index: 100;
text-align: center;
font-family: $main-font;
font-size: 1vw;
font-weight: 300;
padding-bottom: 2%;
}

li{
display: inline;
}

ul{
padding-bottom: 15px;
}

nav li{
padding-left: 30px;
display: inline;
padding-right: 20px;
}

a{
text-decoration:none;
color: $main-txt-color;
}

.navtext{
font-family: $nav-font;
font-size: 4vw;
}

.nav-logo{
width: 7vw;
padding-left: 25px;
display: inline-block;
-webkit-touch-callout: none; /* iOS Safari */
-webkit-user-select: none; /* Safari */
 -khtml-user-select: none; /* Konqueror HTML */
   -moz-user-select: none; /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
        user-select: none; /* Non-prefixed version, currently
                              supported by Chrome, Edge, Opera and Firefox */
}

.hr{
opacity: 0.4;
width: 50%;
margin: auto;
}

.smlogo{
text-align: left;
}

.logos{
width: 30px;
padding-right: 5px;
opacity: .8;

&:hover{
opacity: 1;
}
}

.phone{
display: inline-flex;
align-items: center;
position: absolute;
text-align: right;
right: 2%;
font-size: 15px;
font-weight: 600;
}