@import '../../components/utilities/scssvariables.scss';

.grid-wrapper{
display: grid;
grid-template-columns:1fr 1fr;
grid-template-areas: 
    "A B"
    "A C";
margin: auto;
text-align: center;
grid-gap: 5px;
}

.item1 {
grid-area: A;
width: 100%;
height: 100%;
background: url(../../assets/heroimg.jpg) no-repeat  50% 0px / cover;
box-shadow: inset 0 0 0 300px rgba(36, 70, 105, 0.3);
}

.item2 {
grid-area: B;
max-width: 100%;
max-height: 100%;
cursor: pointer;
}

.item3 {
grid-area: C;
background:  #44495D;
color: $invert-txt-color;
width: 100%;
height: 100%;
}

.mainimg{
width: 45vw;
height: 30vw;
display: inline-block;
vertical-align: bottom;
}

.logoslidt{
width: 40vw;
height: 20vw;
padding-top: 15%;
padding-left: 3%;
}

.smallimg{
width: 100%;
height: 100%;
display: block;
background: url(../../assets/sortiment.jpg) no-repeat  50% 0px / cover;
box-shadow: inset 0 0 0 200px rgba(36, 70, 105, 0.3);

&:hover{
box-shadow: inset 0 0 0 150px rgba(36, 70, 105, 0);
}
}

.but-sort{
height: 3.5vw; 
width: 10vw;
position: relative;
top: 40%;
font-family: 'Montserrat', sans-serif;
font-size: 1vw;
background-color: white;
padding: 0;
border: none;
outline: none;
cursor: pointer;
}

.p-main{
position: relative;
top: 30%;
font-size: 1vw;
}

.cat-header{
padding-top: 7%;
padding-bottom: 2%;
text-align: center;
font-family: 'Montserrat', sans-serif;
font-size: 2.5vw;
}

.sortiment{
padding-top: 5%;
z-index: 2000;
position: relative;
display: grid;
grid-template-columns: 1fr 18.5vw 18.5vw 18.5vw 18.5vw 1fr;
grid-gap: 0px;
grid-template-areas: ". boxOne boxTwo boxThree boxFour ." 
                      ". boxFive boxSix boxSeven boxEight .";
align-items: start;
justify-items: center;
margin: auto;
font-family: 'Montserrat', sans-serif;
}

.images{
width: 20vw;
height: 20vw;
display: block;
cursor: pointer;
}

